<template>
  <div id="app">
    <h2>Make your life count</h2>
    <hr />
    <div class="counter-list">
      <counter 
      v-for="(date,i) in counterData" 
      :key="i" 
      :date="date" 
      :index="i" 
      :sexy="sexy" 
      :maxLifeTime="maxLifeTime"
      :scroll="scrolling" 
      />
      <div class="more">
        <span v-for="i in 3" :key="i"></span>
        <i>Treasure every moment you have</i>
      </div>
    </div>
    <commit />
    <div class="copyright">
      Designed by Vicco Wang @ 2019
    </div>
  </div> 
</template>
<script>
import { lifeExpectancy, throttle } from './utils'
import Counter from './components/counter'
import Commit from './components/commit'

export default {
  name: 'app',
  data () {
    return {
      country: 'China',
      sexy: 0,
      counterData: [
        '1950',
        '1960',
        '1970',
        '1980',
        '1990',
        '2000',
        '2010',
      ].reverse(),
      time: '',
      scrolling: null
    }
  },
  computed: {
    maxLifeTime () {
      const country = lifeExpectancy[this.country]
      return this.sexy === 0 ? country['male'] : country['female']
    }
  },
  mounted () {
    document.addEventListener('scroll', throttle(this.initScrollHandle, 300) )
  },
  methods: {
    initScrollHandle(event) {
      this.scrolling = event.timeStamp
    } 
  },
  components: {
    Counter,
    Commit
  }
}
</script>

